<template>
  <div id="landing"> 
    <!-- <Slider class="slidder"/> -->
      <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="fullPage"></loading>
    <Slider2 class="slidder2"/>
    <winning-number />
    <welcome-note /> 
    <trending-games />
    <top-games />
    <navigation />
  </div>
</template>

<script> 
import WelcomeNote from "@/components/Landing/WelcomeNote.vue";
import TrendingGames from "@/components/Landing/TrendingGames.vue";
import TopGames from "@/components/Landing/TopGames.vue";
import Navigation from "@/components/Landing/Navigation.vue";
// import Slider from "@/components/Landing/Slider.vue";
import Slider2 from "@/components/Landing/slider2.vue";
import WinningNumber from "@/components/Landing/WinningNumber.vue";
import axios from 'axios';
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";

export default {
  name: "components",
  components: { WelcomeNote, TrendingGames, TopGames, Navigation, Slider2, WinningNumber, Loading },

   data(){
    return {  
      results: [],
      isLoading: false,
      fullPage: true
    }
  },
  mounted() {
    this.isLoading = true;
    this.getResult();
     // console.log(res)
      // simulate AJAX
      setTimeout(() => {
        this.isLoading = false;
      }, 4000);
  },

  methods: {
    getResult() {
      axios.post("https://middlewarez.herokuapp.com/api/v1/activites/game/schedule?as=game&task=schedule&auth=55904993399349495bb6fdd")
      .then((response) => {

       const winningNumber = response.data.data;
       this.results = winningNumber
      }).catch( error => { console.log(error); });
    }
  }
};
</script>

